#main{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}
#searchDiv{
    -ms-flex: 1 1;
        flex: 1 1;
    text-align: center;
    margin-top: 5vh;
    background-color: #00000085;
    max-width: 30vw;
    margin: auto;
    margin-top: 10vh;
    padding: 20px;
    border-radius: 20px;   
}

#searchDiv label{
    font-size: 40px;
    color: white;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}

#searchInput{
    text-align: center;
    height: 50px;
    font-size: 22px;
    border-radius: 10px;
    text-transform: uppercase;
}
#submitBtn{
    width: 120px;
    margin: auto;
    background-color: #02154a;
    color: white;
    margin-top: 10px;
    padding: 10px;
    border-radius: 20px;
}

#submitBtn:hover{
    background-color: white;
    color: black;
    border: none;
    font-weight: bold;
}

#searchInput:focus::-webkit-input-placeholder {
    opacity: 0;
}

.custWI{
    font-size: 50px;
    color: blue;
}

#main::-webkit-scrollbar {
    width: 1em;
}
 
#main::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}
 
#main::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  outline: 1px solid slategrey;
}


